.support-ct__form{
	margin-bottom: 80px;
	&:last-child{
		margin-bottom: 0;
	}
	.btn_style_04{
		margin-top: 27px;
	}
	&-item{
		padding-bottom: 9px;
		margin-bottom: 9px;
		border-bottom: 1px solid #E0E1E3;
		@include flex();
	}
	&-ttl{
		width: 220px;
		box-sizing: border-box;
		padding-left: 15px;
		padding-top: 8px;
		font-weight: bold;
		&--02{
			padding-top: 0;
		}
	}
	&-ct{
		// width: calc(100% - 220px);
		.tableform_select{
			min-width: 188px;
		}
	}
	&-row{
		@include flex(0,center);
	}
	&-textarea{
		@include flex(0,end);
		textarea{
			width: 800px;
			height: 151px;
			resize: none;
			overflow-y: auto;
			margin-left: 3px;
		}
		.support-ct__form-note{
			width: calc(100% - 800px);
			padding-left: 20px;
			@include font-size(12);
		}
	}
	&-note{
		color: #969696;
		font-weight: 500;
		padding-left: 20px;
		display: block;
		width: calc(100% - 188px);
		@include font-size(13);
	}
	&-flex{
		@include flex();
		// margin-left: -4px;
	}
	&-img{
		width: 250px;
		box-sizing: border-box;
		overflow: hidden;
		.type-video{
			display: none;
		}
		&.on{
			.type-video{
				display: block;
			}
			.type-img{
				display: none;
			}
		}
		img{
			// border: 1px solid #969696;
			width: 100%;
			padding-bottom: 9px;
		}
	}
	&-file{
		width: calc(100% - 250px);
		padding-left: 27px;
		box-sizing: border-box;
		label{
			cursor: pointer;
			padding-top: 6px;
			padding-right: 23px;
			box-sizing: border-box;
			margin-bottom: 7px;
			background: #FAFAFA;
			height: 86px;
			text-align: center;
			color: #969696;
			font-weight: 500;
			position: relative;
			@include flex(center,center);
			@include font-size(14);
			transition: all 0.3s;
			&:hover{
				opacity: 0.7;
				transition: all 0.3s;
			}
			span{
				text-decoration: underline !important;
			}
			&::before{
				content: '';
				height: calc(100% - 22px);
				width: calc(100% - 22px);
				border: 1px dashed #707070;
				top: 10px;
				left: 10px;
				position: absolute;
			}
		}
		input[type=file]{
			opacity: 0;
			position: absolute;
			z-index: -1;
			width: 0;
		}
	}
	&-delete{
		background: #F2F0EE;
		border: 1px solid #E0E1E3;
		border-radius: 4px;
		box-sizing: border-box;
		height: 25px;
		width: 60px;
		padding: 2px;
		margin-top: 10px;
		margin-left: auto;
		cursor: pointer;
		font-weight: 500;
		@include font-size(14);
		@include flex(center,center);
		color: #F04850;
	}
	&-download{
		background: #F2F0EE;
		border: 1px solid #E0E1E3;
		border-radius: 4px;
		box-sizing: border-box;
		height: 25px;
		width: 92px;
		padding: 2px;
		margin-top: 10px;
		font-weight: 500;
		margin-left: 0;
		cursor: pointer;
		@include font-size(14);
		margin-right: 10px;
		@include flex(center,center);
		color: #464646;
	}
	&-btn{
		@include flex(end,center);
		// margin-right: 15px;
		.support-ct__form-delete{
			margin-left: 0;
		}
	}
	&-desc{
		color: #969696;
		line-height: 2.3;
		padding-left: 10px;
		font-weight: 500;
		@include font-size(13);
		.type-video{
			display: none;
		}
		&.on{
			.type-video{
				display: block;
			}
			.type-img{
				display: none;
			}
		}
		span{
			color: #464646;
			display: block;
			padding-bottom: 10px;
			line-height: 1.9;
			@include font-size(14);
		}
		small{
			display: inline-block;
			color: #5BC0DE;
			font-weight: bold;
			padding-right: 30px;
			margin-left: -10px;
			position: relative;
			@include font-size(14.5);
			&.no-before{
				&:after{
					display: none;
				}
			}
			&:after{
				content: '';
				width: 20px;
				height: 19px;
				background: url('../img/T039-1/icon-01.png') center no-repeat;
				background-size: contain;
				position: absolute;
				right: 0;
				top: 7px;
			}
		}
	}
	&-date{
		font-weight: 500;
		color: #464646;
		@include font-size(14);
		span{
			color: #969696;
			margin-left: 40px;
			@include font-size(13);
		}
	}
}

