.flex-between{
	@include flex(between,center);
}

#page-T035{
	.looksort_table__note{
		margin-top: 0;
		// padding-right: 128px;
		@include font-size(13);
	}
	.form-narrow__input{
		width: 625px;
	}
}
.looksort_table__note-left{
	text-align: left;
}

.flex-center{
	width: 100%;
	@include flex(0,center);
	.form-narrow__date{
		width: 340px;
		margin-right: 11px;
	}
	.form-narrow__select{
		margin-right: 11px;
		width: 93px;
	}
	.form-narrow__search{
		margin-right: 14px;
		width: 226px;
	}
	.form-narrow__input-width{
		width: 535px;
	}
}

.form-narrow__input-full{
	width: 1200px;
}

#page-T077{
	.form_part_txt{
		width: 1200px;
		height: 87px;
		resize: none;
		margin-top: 5px;
	}
	.form_part_unit{
		width: 160px;
	}
}

#page-T077{
	.form-edit {
		margin-left: 0;
	}
}

.form-narrow__input--blue{
	border: 1px solid #78BEDB;
}

.js-show-search{
	position: relative;
}

.js-box-search{
	display: none;
	position: absolute;
	max-width: 222px;
    width: 100%;
    border: 1px solid #E0E1E3;
    top: 50px;
    z-index: 9;
    left: 22px;
    background: white;
	.form-narrow__check-item{
		margin-right: 0;
		width: 100%;
		padding: 8px;
		border-bottom: 1px solid #E0E1E3;
		&:last-child{
			border-bottom: 0;
		}
	}
}

.looksort_table_pdf{
	display: none;
	&.on{
		display: contents;
	}
}
.looksort_table_mp4{
	&.on{
		display: none;
	}
}