#page-T011,#page-T013{
	.col2_block_main--block{
		width: calc(100% - 220px);
	}
	.table_style_02{
		th,td{
			padding-top: 0.88%;
			padding-bottom: 0.88%;
		}
		.looksort_table{
			*{
				box-sizing: border-box !important;
			}
			&_name{
				width: 250px;
				font-weight: bold;
				span{
					padding: 4px 24px 4px 0;
					background: url('../img/common/icn_help.png') no-repeat right center;
    				background-size: 20px auto;
				}
			}
			&_results{
				width: 205px;
			}
			&_last{
				width: 243px;
				padding-left: 34px;
				box-sizing: border-box;
			}
			&_cumulative{
				width: calc(100% - 698px);
			}
		}
	}
}
.achievement-flex{
	@include flex(0,end);
	flex-wrap: wrap;
	width: 100%;
	min-width: 535px;
	margin-bottom: 16px;
	margin-top: -14px;
	*{
		box-sizing: border-box;
	}
	&__item{
		margin-top: 10px;
		margin-right: 26px;
		width: 365px;
		border-radius: 4px;
		padding: 17px 18px 15px;
		border: 5px solid #F2F0EE;
		&:nth-child(2){
			margin-right: 0;
			width: 352px;
		}
		a{
			@include flex(0,center);
			text-decoration: none;
		}
	}
	&__txt{
		width: 55%;
		font-weight: bold;
		color: #464646;
		padding-right: 10px;
		@include font-size(14.5);
		span{
			margin-top: 6px;
			display: block;
			color: #969696;
			font-weight: 500;
			@include font-size(12);
		}
	}
	&__number{
		width: 45%;
		text-align: right;
		color: #464646;
		font-weight: 500;
		@include font-size(30);
	}
	&__desc{
		color: #464646;
		font-weight: 500;
		padding-left: 16px;
		//width: calc(100% - 743px);
		@include font-size(14);
	}
	&--02{
		.achievement-flex{
			&__item{
				@include flex(0,0,wrap);
				width: 696px;
				padding: 24px 22px 24px;
				.form_part{
					//margin-right: 30px;
					margin-bottom: 10px;
					margin-right: 4%;
					@include flex();
					&:first-child{
						width: 46%;
						max-width: 215px;
					}
					input{
						height: 40px;
					}
					&_name{
						padding: 15px 0 0 10px;
						font-weight: 500;
					}
					&_btn01{
						width: 170px;
					}
				}
			}
		}
	}
}