#page-T014{
	.table_style_02{
		tr:nth-child(odd) td {
			background-color: #fff;
		}
		tr:nth-child(even) td {
			background-color: #FAFAFA;
		}
		th{
			padding: 1.4% 1.5% !important;
			span{
				color: #969696;
				font-weight: 500;
				margin-left: 5px;
				@include font-size(13);
			}
		}
		td,th{
			&:nth-child(1){
				padding-left: 30px !important;
			}
		}
		td{
			height: 49px;
			vertical-align: middle;
			box-sizing: border-box;
		}
		.todolist_table_priority{
			width: 141px;
			.tableform_input{
				width: 143px;
				margin-left: -10px;
			}
		}
		.todolist_table_details{
			width: 160px;
			.tableform_input{
				width: 134px;
			}
		}
		.todolist_table_status{
			width: 234px;
			&-flex{
				@include flex(between,center);
			}
			.tableform_input{
				width: 86px;
				text-align: right;
			}
			&__right{
				width: 62px;
				text-align: right;
			}
		}
		.todolist_table_btn{
			width: 375px;
			&-flex{
				@include flex(between,center);
			}
			.tableform_input{
				width: 193px;
			}
		}
		.todolist_table_txt{
			font-weight: 500;
			@include font-size(13);
		}
	}
}

.flex-box{
	align-items: flex-start;
	// width: 1577px;
	.todolist{
		// width: calc(100% - 424px);
		width: 72%;
		box-sizing: border-box;
		padding-right: 25px;
	}
	.date-box{
		@include flex(0,center);
		margin-bottom: 20px;
		padding-left: 10px;
		*{
			box-sizing: border-box;
		}
		&__ttl{
			color: #464646;
			font-weight: bold;
			width: 30px;
		    margin-right: 16px;
			@include font-size(14.5);
		}
		&__list{
			@include flex(0,center);
			&-item{
				margin-right: 40px;
				position: relative;
				&:last-child{
					margin-right: 45px;
					&::before{
						content: '〜';
						position: absolute;
						left: -27px;
						top: 50%;
						transform: translateY(-50%);
						position: absolute;
						font-weight: bold;
						@include font-size(14.5);
					}
				}
				.form_part_date{
					width: 209px;
				}
			}
		}
	}
	.form-box{
		// width: 423px;
		width: 28%;
		box-sizing: border-box;
		margin-right: 10px;
		box-sizing: border-box;
		padding: 26px 25px 20px 19px;
		background: rgba(#5BC0DE,0.3);
		.form_part_date{
			width: 100%;
			margin-left: -5px;
		}
		&__flex{
			@include flex();
		    margin-bottom: 11.5px;
			&:last-child{
				margin-bottom: 0;
			}
		}
		&__ttl{
			width: 95px;
			padding-right: 5px;
			padding-top: 7px;
			font-weight: bold;
		}
		&__ct{
			width: calc(100% - 95px);
			.radio-flex{
				@include flex();
				.pure-input{
					transform: translateY(5px);
					margin-right: 15px;
					font-weight: 500;
				}
			}
			.form_part_txt{
				font-weight: 300;
				height: 67px;
				resize: none;
				overflow-y: auto;
			}
			.form_part_unit{
				width: 146px;
				margin-left: -5px;
			}
		}
		.btn_style_04{
			margin: 0 auto;
		}
	}
	&--02{
		position: relative;
		z-index: 9;
		.form-narrow{
			&__item{
				flex-wrap: nowrap;
			}
			&__txt{
				min-width: 30px;
			}
		}
	}
}

.looksort_table_flex{
	@include flex(0,center);
	.statuschips{
		margin-left: 62px;
	}
}

//css page-T017-edit

#page-T017-edit{
	.form_part_com{
		@include font-size(12);
	}
	.looksort_table_info--top{
		vertical-align: top;
		padding-top: 8px !important;
	}
}