#page-T094{
	.report-block__item-ttl {
		width: 166px;
		padding-top: 5px;
		letter-spacing: 1px;
	}
	.report-block__item-ct {
		width: calc(100% - 166px);
		.statuschips{
			position: static;
		}
		.form_part_textarea{
			resize: none;
		}
	}
	.report-block__item{
		padding: 7px 0 7px 23px;
	}
	.form_part_select {
		width: 242px;
	}
	.report-block__right{
		text-align: right;
		margin-top: -2px;
		margin-bottom: -10px;
	}
	.form-edit__card {
		padding: 28px 42px 10px;
	}
	.form_block--02 .form_part:nth-child(1) {
		width: calc(100% - 200px);
		.form_part_txt_memo {
			width: 100%;
			resize: none;
		}
	}
}

