.table_style__03{
	min-width: 1280px;
	.looksort_table_id{
	    width: 138px;
	}
	.looksort_table_date{
		width: 203px;
	}
	.looksort_table_type{
		width: 197px;
	}
	.looksort_table_status{
		width: 164px;
	}
	.looksort_table_score{
		width: 128px;
	}
	.looksort_table_result{
		width: 114px;
	}
	.looksort_table_notify{
		width: 130px;
	}
	.looksort_table_memo{
		text-align: right;
		width: calc(100% - 1074px);
		button{
			margin-left: auto;
		}
		a{
			text-decoration: none !important;
		}
	}
	tr:nth-child(odd) td {
		background-color: #fff !important;
	}
	tr:nth-child(even) td {
		background-color: #FAFAFA !important;
	}
}

.flex-end{
	@include flex(end);
}

.color-black{
	*{
		color: #000 !important;
	}
}

.table-hide{
	display: none;
	&.on{
		display: block;
	}
}
.list-select{
	.add-info__select{
		display: none !important;
		&.show{
			display: block !important;
		}
		textarea{
			display: none !important;
			&.show{
				display: block !important;
			}
		}
	}
}

.item-form{
	display: none;
	&.show{
		display: block;
	}
}