.point-block{
	margin-bottom: 20px;
	&__left{
		margin-right: 30px;
		small{
			@include font-size(14.5);
			font-weight: 700;
			display: block;
		}
		span{
			display: flex;
			justify-content: flex-end;
			min-width: 185px;
			background: #FED3CF;
			border-radius: 4px;
			padding: 2px 20px 2px 20px;			
			@include fts(23,35);
			font-weight: 500;
		}
	}
	&__right{
		small{
			@include font-size(14.5);
			font-weight: 700;
			display: block;
		}
		span{
			display: inline-flex;
			background: #F2F0EE;
			border-radius: 4px;
			padding: 9px 10px;
			font-weight: 500;
			margin-bottom: 10px;
			margin-right: 10px;
		}
	}
}
.point-flex{
	@include mq(xxl){
		flex-wrap: wrap;
	}
	.statuschips{
		width: 135px;
		margin-bottom: 15px;
		@include mq(xxl){
			width: 100%;
		}
	}
	.achievement-flex{
		@include mq(xxl){
			width: 100%;
			flex-wrap: wrap;
		}
		&__item{
			max-width: 696px;
			@include mq(xxl){
				width: 100%;
				margin-bottom: 15px;
			}
		}
		&__desc{
			@include mq(xxl){
				width: 100%;
				padding-left: 0;
			}
		}
	}
	.form_part{
		@include mq(lg){
			margin-bottom: 15px;
		}
		select{
			color: #969696;
			&.focus{
				color: #000;
			}
		}
	}
}

