.web-manager-form{
	&__input{
		width: 1150px;
	}
	&__textarea{
		width: 1150px;
		height: 274px;
	}
	&__control{
		padding-top: 30px;
		.btn_style_07{
			&--outline{
				background: transparent;
			}
		}
	}
}
.box-option-all{

}

.box-option-user{
	display: none;
}
.box-option-other{
	display: none;
}



.box-option-personal{
	display: none;
	&__delivery{
		position: relative;
	}
	&__wrapper{
		width: 230px;
	}
	&__name{
		position: relative;
		width: 210px;
		//height: 40px;
		//border: 1px solid #78BEDB;
		//@include flex(0, center);
		//padding: 0 10px;
		//@include font-size(14);
		//font-weight: 300;
		//color: #969696;
		//span{
		//	width: 16px;
		//	margin-right: 7px;
		//}
		input{
			width: 100%;
			padding-left: 30px;
			border: 1px solid #78BEDB;
		}
		@include p-after(16px, 16px){
			background: url("../img/common/ico-search.png") no-repeat center/100%;
			top: calc(50% - 8px);
			left: 10px;
		}
	}
	&__ct{
		position: absolute;
		top: 53px;
		left: 0;
		width: 230px;
		border: 1px solid #E0E1E3;
		z-index: 2;
		background: #fff;
		display: none;
		.form-narrow{
			&__check{
				&-item{
					margin-right: 0;
					width: 100%;
					padding: 10px 10px;
					border-bottom: 1px solid #E0E1E3;
					&:last-of-type{
						border-bottom: 0;
					}
				}
			}
		}
	}
	&__result{
		padding-top: 15px;
		li{
			display: inline-block;
			padding: 10px 17px;
			border: 1px solid #E0E1E3;
			background: #fff;
			border-radius: 4px;
			margin-right: 15px;
			margin-bottom: 15px;
			.close{
				display: inline-block;
				width: 15px;
				margin-left: 23px;
				cursor: pointer;
			}
		}
	}
}


.add-info__select-pd{
	padding: 10px 0;
	.form_part_select{
		margin-bottom: 10px;
	}
}