.recruitment-form{
	//min-width: 1510px;
	width: 100%;
	.cmn-ttl-01{
		margin-bottom: 10px;
	}
	&__item{
		@include flex(0,0,wrap);
		border-bottom: 1px #E0E1E3 solid;
		&.add{
			display: flex !important;
		}
		.reset-form{
			width: 100%;
			@include flex(0,0,wrap);
		}
	}
	&__ttl{
		width: 201px;
		font-weight: bold;
		padding-top: 15px;
		padding-bottom: 7px;
		padding-left: 32px;
		span{
			display: block;
			margin-bottom: 5px;
		}
		input{
			height: 40px;
			width: 90%;
		}
	}
	.form_part_textarea{
		@include flex(start,0,wrap);
		gap: 0 10px;
		margin-top: 0;
		.form_part_txt{
			width: 72%;
			max-width: 786px;
			@include font-size(13);
			line-height: 1.53;
			&--02{
				width: 1120px;
				max-width: inherit;
			}
			&.--l{
				width: 100%;
			}
		}
		.form_part_note{
			margin-top: 5px;
			//width: calc(100% - 800px);
			width: calc(28% - 10px);
			@include font-size(12);
			font-weight: 500;
			color: #969696;
		}
	}
	&__ct{
		width: calc(100% - 233px);
		padding-top: 7px;
		padding-bottom: 7px;
		min-height: 38px;
		&-txt{
			padding-top: 9px;
			display: inline-block;
		}
		&--flex{
			@include flex(0,end);
			.btn_style_03{
				margin-left: 15px;
			}
			.form_part_textarea{
				width: calc(100% - 80px);
				.form_part_txt{
					width: 100%;
				}
			}
		}
		&-note{
			@include font-size(13);
			color: #969696;
		}
	}
	&__control{
		margin-top: 20px;
		padding-bottom: 30px;
		margin-bottom: 0;
		border-bottom: 1px #E0E1E3 solid;
		.btn_add{
			padding-left: 15px;
		}
	}
	&__bg{
		width: 182px;
		height: 120px;
		//margin-top: -8px;
		background: #ECF6F8;
		border: 0;
	}
	&--02{
		.recruitment-form{
			&__ct{
				&-txt{
					margin-right: 30px;
				}
				&-note{
					@include font-size(12);
					font-weight: 500;
					@include mq(xl){
						padding-top: 5px;
					}
				}
				&--02{
					.form_part_textarea{
						align-items: end;
					}
					.form_part_note{
						@include font-size(12);
						font-weight: 500;
						color: #969696;
						@include mq(xxl){
							width: 100%;
							padding-top: 10px;
						}
					}
				}
			}
		}
		.form_part_select{
			margin-right: 30px;
		}
	}
}

.recruitment-form__control--left{
	.btn_add{
		margin-left: 76px;
	}
}

#page-T024{
	.table_style_02--style5 .looksort_table_info {
		min-width: 220px;
	}
}

.table-scroll{
	overflow-x: auto;
	width: 100%;
	.recruitment-form{
		min-width: 1000px;
	}
}