 .add-info__select--550{
	width: 550px;
 }

 .form_part_date_input--340{
	width: 340px;
	font-weight: 500;
	&::placeholder{
		font-weight: bold;
	}
 }

 .list-file{
	padding: 10px 0 10px 0;
	margin-right: -13px;
	@include flex();
	&__item{
		margin-right: 16px;
		width: calc(50% - 8px);
		max-width: 455px;
		&:last-child{
			margin-right: 0;
		}
		label{
			padding: 23px 47px 32px 51px;
			position: relative;
			z-index: 2;
			cursor: pointer;
			background: #FAFAFA;
			text-align: center;
			position: relative;
			z-index: 10;
			font-weight: bold;
			display: block;
			span{
				margin-top: 12px;
				color: #969696;
				font-weight: 500;
				display: block;
				@include font-size(14);
				span{
					display: inline-block;
					margin-top: 0;
					text-decoration: underline;
				}
			}
			&::before{
				content: '';
				width: calc(100% - 22px);
				height: calc(100% - 22px);
				left: 10px;
				top: 10px;
				z-index: -1;
				position: absolute;
				border: 1px dashed #969696;
			}
		}
	}
	&__img{
		margin-top: 14px;
	}
 }

 .mbpc-28{
	margin-bottom: 28px;
	padding-left: 0;
 }

 .support-ct__form-ct--02{
	padding: 10px 0;
 }

 .flex-img{
	@include flex();
	&__item{
		margin-right: 40px;
		max-width: 375px;
		width: 100%;
		text-align: center;
		&:last-child{
			margin-right: 0;
		}
		img{
			width: 100%;
			margin-bottom: 20px;
		}
		p{
			color: #464646;
			font-weight: bold;
			@include font-size(13);
		}
	}
 }

 .looksort_table__txt{
	padding-right: 70px;
 }

 .looksort_table__note{
	display: block;
	text-align: right;
	color: #969696;
	font-weight: 500;
	margin-top: 10px;
	@include font-size(12);
 }

 .looksort_table__right{
	text-align: right;
	.statuschips{
		font-weight: 500;
	}
 }

 .vertical-top{
	transform: translateY(5px);
	vertical-align: top !important;
 }

 .form_part_textarea{
	margin-top: 5px;
 }

 .border-0{
	border-bottom: 0 !important;
 }

.table_style_02--medium{
	tbody tr td{
		font-weight: 500;
	}
 }

