 //css page-T049-edit
 #page-T049-edit{
	.form_part_select{
		width: 240px;
	}
	.looksort_table__flex{
		@include flex(0,center);
		.statuschips{
			margin-left: 10px;
			span{
				text-decoration: none;
			}
		}
	}
	.form-narrow__select{
		width: 84px;
	}
	.form_part_unit {
		width: 145px;
	}
	.js-lable{
		.off{
			display: none;
		}
		.on{
			display: revert;
		}
		&.on{
			.off{
				display: table-cell;
			}
			.on{
				display: none;
			}
		}
	}
	.js-check-radio{
		@include flex(0,center);
		.statuschips {
			margin-left: 20px;
			span{
				text-decoration: none;
			}
		}
		
		&.on{
			.looksort_table_info__address{
				@include flex(0,center);
			}
			.looksort_table_info__note{
				display: none;
			}
		}
		.looksort_table_info__note{
			color: #969696;
			font-weight: 500;
			@include font-size(13);
		}
		.looksort_table_info__address{
			display: none;
			span{
				margin-right: 24px;
				color: #464646;
				font-weight: bold;
				@include font-size(14.5);
			}
			.form-edit__input{
				width: 325px;
			}
		}
	}
	.form-narrow__item {
		margin-bottom: 0;
		padding-bottom: 0;
		border-bottom: 0;
	}
	.form-edit__input{
		width: 766px;
	}
 }
 
 
 .file-option{
	 .item{
		 display: none;
		 &.active{
			 display: block;
		 }
		 &.show{
			 display: block;
		 }
	 }
 }