.looksort-yen{
	@include flex(between);
	padding: 7.5px 10px;
	border: 3px solid #F6D5D0;
	color: #464646;
	line-height: 1.2;
	width: 235px;
	&__name{
		display: inline-block;
		font-weight: bold;
		@include font-size(14.5);
	}
	&__number{
		display: inline-block;
		font-weight: 500;
		@include font-size(14);
	}
}

