.support-ct{
	text-align: left;
    width: 100%;
    display: block;
	.cmn-ttl-01{
		margin-bottom: 8px;
		padding-left: 12px;
	}
	&__table{
		margin-bottom: 40px;
		&-item{
			@include flex(0,start);
			padding-bottom: 9px;
			margin-bottom: 9px;
			border-bottom: 1px solid #E0E1E3;
		}
		&-ttl{
			box-sizing: border-box;
			width: 222px;
			color: #464646;
			font-weight: bold;
			padding: 5px 15px 5px 0;
			padding-left: 45px;
			cursor: pointer;
			transition: all 0.3s;
			@include font-size(14);
			position: relative;
			&:hover{
				transition: all 0.3s;
				opacity: 0.7;
			}
			&::before{
				content: '';
				width: 29px;
				height: 29px;
				border-radius: 4px;
				background: #F2F0EE;
				border: 1px solid #DDDDDD;	
				position: absolute;
				left: 0;
				top: 0;			
			}
			&::after{
				content: "";
				width: 6px;
				height: 6px;
				border-right: 2px solid #464646;
				border-bottom: 2px solid #464646;
				position: absolute;
				top: 10px;
				left: 11px;
				transform: rotate(45deg);
			}
			&--off{
				&::after{
					top: 13px;
					transform: rotate(-135deg);
				}
			}
		}
		&-ct{
			padding-top: 6px;
			box-sizing: border-box;
			width: calc(100% - 222px);
		}
		&-title{
			color: #464646;
			font-weight: bold;
			padding-top: 11px;
			display: block;
			margin-bottom: 8px;
			@include font-size(14);
		}
		&-accordion{
			margin-top: 15px;
			&.none{
				display: none;
			}
		}
	}
}

.list-checkbox{
	@include flex(0,0,wrap);
	// margin-bottom: 10px;
	&__item{
		min-width: 93px;
		// padding-right: 3px;
		padding-right: 10px;
		margin-bottom: 12.5px;
		&:last-child{
			margin-bottom: 0;
		}
		&--full{
			width: 100%;
			margin-bottom: -15px;
			// margin-bottom: 15px !important;
		}
	}
}

.checkbox-custom{
	color: #464646;
	font-weight: 500;
	@include font-size(14);
	display: block;
	position: relative;
	padding-left: 30px;
	cursor: pointer;
	user-select: none;
	input {
		position: absolute;
		opacity: 0;
		cursor: pointer;
		height: 0;
		width: 0;
	}
	.checkmark {
		position: absolute;
		top: 1px;
		left: 0;
		height: 20px;
		width: 20px;
		box-sizing: border-box;
		background-color: #FAFAFA;
		border-radius: 4px;
		border: 1px solid #E0E1E3;
		&:after {
			content: "";
			position: absolute;
			display: none;
			left: 6px;
			top: 1px;
			width: 6px;
			height: 12px;
			border: solid white;
			border-width: 0 2px 2px 0;
			transform: rotate(45deg);
		}
	}
	input:checked ~ .checkmark {
		background-color: #5BC0DE;
	}
	input:checked ~ .checkmark:after {
		display: block;
	}
	&--02{
		.checkmark{
			width: 22px;
			height: 22px;
			top: 0;
			&:after{
				width: 5px;
				border-width: 0 3px 3px 0;
			}
		}
	}
}
.pt-23{
	padding-top: 8px !important;
	margin-top: 0 !important;
}