#page-T013{
	.daily_data{
		height: 38px;
		margin-bottom: 34px;
		.prev,.next{
			margin-top: 10px;
		}
	}
	.daily_data_inr{
		width: 192px;
		height: 100%;
		@include font-size(21);
	}
	.achievement-flex__item {
		width: 269px;
		.achievement-flex__txt {
			width: 45%;
		}
		.achievement-flex__number {
			width: 55%;
		}
	}
	.achievement-flex {
		margin-bottom: 23px;
	}
	.table_style_02{
		td{
			vertical-align: top !important;
			height: 102px;
			padding-top: 1.3%;
		}
		tr{
			&:nth-child(odd) td{
				background: #fff;
			}
			&:nth-child(even) td{
				background: #FAFAFA;
			}
		}
		td,th{
			&:last-child{
				position: relative;
				z-index: 2;
				&::before{
					content: '';
					width: 100%;
					height: 100%;
					background: rgba(#FED3CF,0.2);
					top: 0;
					left: 0;
					position: absolute;
					z-index: -1;
				}
			}
		}
		.looksort_table_id{
			width: 80px;
			text-decoration: underline;
		}
		.looksort_table_start{
			width: 154px;
		}
		.looksort_table_end{
		    width: 160px;
		}
		.looksort_table_supporter{
			width: 144px;
			a{
				display: block;
				text-decoration: underline;
			}
		}
		.looksort_table_user{
			width: 150px;
		}
		.looksort_table_time{
			width: 365px;
			&-flex{
				@include flex();
				line-height: 1.714;
			}
			&-left{
				width: 150px;
			}
			&-right{
				width: 215px;
				text-align: right;
			}
		}
		.looksort_table_type{
			width: 100px;
			font-weight: bold;
		}
	}
}

.table_style_02--white{
	tbody tr:nth-child(odd) td {
		background-color: #fff;
	}
}