.audit-block{
	&__date{
		position: sticky;
		top: 0;
		width: 100%;
		
		//position: relative;
		text-align: center;
		max-width: 1490px;
		z-index: 1;
		@include p-before(100%,1px){
			background: #969696;
			left: 0;
			@include v-align();
		}
		span{
			min-width: 230px;
			border: 1px #969696 solid;
			background: #fff;
			@include font-size(14);
			font-weight: 500;
			border-radius: 20px;
			padding: 0 10px;
			display: inline-flex;
			justify-content: center;
			align-items: center;
			position: relative;
			height: 32px;
		}
	}
}
.audit-list{
	overflow-y: auto;
	max-height: 980px;
	max-width: 1490px;
	&__block{
		
	}
	&__item{
		padding-right: 160px;
		margin-bottom: 40px;
		position: relative;
		&:nth-child(2n){
			.audit-list{
				&__ct{
					background: rgba(254,211,207,0.15);
				}
			}
		}
	}
	&__ct{
		margin-top: -9px;
		margin-left: 68px;
		background: rgba(91,192,222,0.15);
		padding: 17px 27px 20px 25px;
		@include fts(13,35);
		font-weight: 500;
		border-radius: 10px;
	}
	&__time{
		color: #969696;
		position: absolute;
		right: 58px;
		bottom: -4px;
		width: 90px;
		span{
			margin-left: 10px;
		}
	}
}
/* width */
.audit-list::-webkit-scrollbar {
	width: 10px;
}
/* Track */
.audit-list::-webkit-scrollbar-track {
	background: #F2F0EE;
	border-radius: 10px 10px 0 0;
}
/* Handle */
.audit-list::-webkit-scrollbar-thumb {
	background: #464646;
	border-radius: 10px 10px 0 0;
}