.settement_manager{
	.tableblock_scroll{
		margin-bottom: 25px;
	}
}

.settement_manager_heading{
	@include flex(between, center);
	margin-bottom: 30px;
	.daily_data{
		height: auto;
		min-width: 190px;
		&_inr{
			width: 190px;
			height: auto;
		}
	}

	&--styles02{
		justify-content: flex-start;
		.daily_data{
			margin-right: 1.5%;
		}
	}
}
.settement_manager_note{
	.looksort_statusdescription{
		max-width: 760px;
	}
}
.settement_manager_payment{
	margin-bottom: 10px;
	padding-left: 12px;
	span{
		margin-right: 10px;
	}
	.btn_style_04{
		min-width: 52px;
		margin-left: 8px;
	}
}

.table_settement_manager{
	tr{
		th{
			&:nth-child(1){
				width: 223px;
				padding: 0.8% 2.2%;
			}
			&:nth-child(2){
				width: 165px;
			}
			&:nth-child(3){
				width: 200px;
			}
			&:nth-child(4){
				width: 270px;
			}
			&:nth-child(5){
				width: 115px;
			}
			&:nth-child(6){
				width: 180px;
				text-align: center;
			}
			&:nth-child(8){
				width: 88px;
			}
			.statuschips{
				font-weight: 700;
			}
		}
	}
	&.looksort_table tbody td{
		padding: 0.12% 1%;
		&:first-child{
			padding-left: 2.1%;
		}
		a{
			line-height: 1;
			text-align: left;
		}
	}

	&--styles02{
		.list-checkbox{
			width: 15px;
			height: 15px;
		}
		tr{
			th, td{
				position: relative;
			}
			th{
				text-align: center;
				&:first-of-type{
					width: 42px;
					padding: 0.8% 1%;
				}
				&:nth-child(2){
					width: 158px;
					text-align: center;
				}
				&:nth-child(3){
					width: 126px;
					text-align: center;
				}
				&:nth-child(4){
					width: 170px;
				}
				&:nth-child(5){
					width: 200px;
				}
				&:nth-child(6){
					width: 136px;
				}
				&:nth-child(7){
					width: 136px;
				}
				&:nth-child(8){
					width: 240px;
					text-align: left;
					padding-left: 2%;
				}
				&:nth-child(9){
					width: 100px;
					text-align: left;
					padding-left: 2%;
					padding-right: 0;
				}
				&:nth-child(9), &:nth-child(10){
					
					@include p-after(100%, 100%){
						background: rgba(#5BC0DE, 0.15);
						top: 0;
						left: 0;
					}
				}
				&:nth-child(10), &:nth-child(11){
					width: 105px;
				}
				&:nth-child(11){
					@include p-after(100%, 100%){
						background: rgba(#FED3CF, 0.2);
						top: 0;
						left: 0;
					}
				}
			}
			td{
				text-align: center;
				&:first-of-type{
					padding-left: 1%;
				}
				&:nth-child(2){
					text-align: left;
				}
				&:nth-child(4){
					.statusicn{
						margin: 0 auto;
					}
				}
				&:nth-child(5){
					text-align: center;
					padding-left: 5%;
				}
				&:nth-child(8){
					text-align: left;
					padding-left: 2%;
				}
				&:nth-child(9){
					text-align: left;
					padding-left: 2%;
					padding-right: 0;
				}
				&:nth-child(9), &:nth-child(10){
					@include p-after(100%, 100%){
						background: rgba(#5BC0DE, 0.15);
						top: 0;
						left: 0;
					}
				}
				&:nth-child(11){
					@include p-after(100%, 100%){
						background: rgba(#FED3CF, 0.2);
						top: 0;
						left: 0;
					}
				}
			}
		}
		&.looksort_table tbody td{
			&:first-child{
				padding-left: 1%;
			}
		}
	}


	&__input{
		width: 294px;
		&--full{
			width: 100%;
		}
	}
	&_deposit{
		text-align: center;
	}
	.form_part_date{
		width: 210px;
	}

	.form_part_unit{
		width: 146px;
	}

}


