.reviews-box{
	padding: 30px 0 42px 15px;
	padding-left: 15px;
	min-width: 1580px;
	@include flex(start,center);
	width: 100%;
	&__star{
		width: 465px;
		box-sizing: border-box;
		@include flex();
	}
	&__number{
		line-height: 1.667;
		width: 90px;
		color: #969696;
		font-weight: 500;
		text-align: center;
		@include font-size(12);
		span{
			border-bottom: 1px solid #969696;
			line-height: 1;
			display: block;
			margin-bottom: 5px;
			font-weight: bold;
			color: #FECC00;
			@include font-size(65);
		}
	}
	&__ranking{
		width: calc(100% - 90px);
		padding-left: 50px;
		&-item{
			@include flex(start,center);
			margin-bottom: 10px;
			.col2_block_side_user_star_icn{
				width: 82px;
				height: 14px;
				&[data-rate="4"]:after {
					width: 65px;
				}
				&[data-rate="3"]:after {
					width: 50px;
				}
				&[data-rate="2"]:after {
					width: 32px;
				}
				&[data-rate="1"]:after {
					width: 16px;
				}
				&::after,&::before{
					height: 14px;
				}
			}
		}
		&-value{
			width: 187px;
			height: 4px;
			background: #F2F0EE;
			border-radius: 3px;	
			margin-left: 12px;
			position: relative;
			&::before{
				content: '';
				position: absolute;
				left: 0;
				top: 0;
				width: 0;
				height: 100%;
				background: #FECC00;
				border-radius: 3px;
			}
			&--01{
				&::before{
					width: 20%;
				}
			}
			&--02{
				&::before{
					width: 28%;
				}
			}
			&--03{
				&::before{
					width: 40%;
				}
			}
			&--04{
				&::before{
					width: 65%;
				}
			}
			&--05{
				&::before{
					width: 100%;
				}
			}
		}
		&-piece{
			width: 40px;
			padding-left: 10px;
			color: #969696;
			font-weight: 500;
			@include font-size(13);
		}
	}
	&__medal{
		width: calc(100% - 465px);
		padding-left: 11.5%;
		box-sizing: border-box;
		margin-top: 28px;
	}
	&__list{
		@include flex(0,center);
		width: 100%;
		margin-bottom: 20px;
		&-item{
			margin-right: 46px;
			&:last-child{
				margin-right: 0;
			}
			&:first-child{
				.reviews-box__list-ct{
					span{
						color: #464646;
					}
				}
			}
			@include flex(0,center);
		}
		&-img{
			width: 48px;
			img{
				width: 100%;
			}
		}
		&-ct{
			padding-left: 6px;
			color: #969696;
			font-weight: 500;
			@include font-size(13);
			span{
				color: #969696;
				font-weight: bold;
				display: block;
				margin-top: -3px;
				@include font-size(14.5);
			}
		}
	}
}
.application-block--flex{
	@include flex(start,start,nowrap);
	width: 100%;
	margin-top: 18px;
	margin-bottom: 16px;
	.form_block{
		width: 223px;
		margin-right: 19px;
		margin-bottom: 0;
		box-sizing: border-box;
		*{
			margin-bottom: 0 !important;
		}
	}
	.application-block__right{
		box-sizing: border-box;
		max-width: 678px;
		width: 100%;
		border-radius: 4px;
		background: #E8FAFF;
		color: #464646;
		font-weight: 500;
		padding: 8px 20px;
		@include font-size(13);
	}
}

#page-T012{
	overflow: hidden;
}

.menu-tab{
	@include mq(xl-min){
		width: 100% !important;
		// min-width: 1000px;
		min-width: 794px;
	}
	&__list{
		margin-left: 45px;
		@include flex();
		&-item{
			margin-right: 27px;
			&.active{
				background: #FAFAFA;
			}
			span{
				cursor: pointer;
				border: 1px solid #E0E1E3;
				border-bottom: 0;
				font-weight: bold;
				min-width: 220px;
				display: block;
				box-sizing: border-box;
				padding: 6px;
				text-align: center;
				@include font-size(14.5);
			}
		}
	}
	&__content{
		width: 100%;
		display: block;
		&-item{
			width: 100%;
			background: #FAFAFA;
			border-bottom: 1px solid #E0E1E3;
			padding: 20px 46px 32px;
			@include flex();
			&:nth-child(2n){
				background: #fff;
			}
			&:nth-child(1){
				border-top: 1px solid #E0E1E3;
			}
		}
		*{
			box-sizing: border-box;
		}
		&-tab{
			opacity: 0;
			visibility: hidden;
			display: none;
			transition: all .2s ease-in;
			&.active{
				opacity: 1;
				display: block;
				visibility: visible;
			}
		}
		&-left{
			// width: 61.5%;
			width: 775px;
		}
		&-right{
			// width: calc(100% - 775px);
			padding-left: 48px;
			margin-top: 13px;
			.form_part_select{
				width: 101px;
			}
		}
		&-top{
			margin-bottom: 19px;
			@include flex(0,start);
		}
		&-user{
		    min-width: 42.5%;
			@include flex(0,center);
			img{
				margin-right: 13px;
				width: 29px;
			}
			span{
				color: #464646;
				font-weight: 500;
				text-decoration: underline;
				@include font-size(14);
			}
		}
		&-name{
			margin-right: 29px;
			color: #464646;
			font-weight: 500;
			@include font-size(14);
		}
		&-date{
			margin-right: 26px;
			color: #464646;
			font-weight: 500;
			@include font-size(14);
		}
		&-desc{
			color: #464646;
			font-weight: 500;
			line-height: 1.6428;
			@include font-size(14);
		}
	}
}