.profile-form{
	padding-top: 24px;
	width: 100%;
	&__item{
		@include flex();
		border-bottom: 1px #E0E1E3 solid;
		padding-bottom: 18px;
		margin-bottom: 27px;
		&:last-child{
			border-bottom: none;
		}
		&.add{
			display: flex !important;
			.profile-form{
				&__ttl{
					@include font-size(0);
					height: 0;
					overflow: hidden;
					opacity: 0;
					visibility: hidden;
				}
			}
		}
	}
	&__ttl{
		width: 148px;
		font-weight: 700;
		padding-left: 15px;
	}
	&__img{
		width: 260px;
		img{
			margin-bottom: 10px;
		}
		.btn_add{
			left: 0;
			margin: 30px 0 0 0;
		}
	}
	&__ct{
		width: calc(100% - 400px);
		padding-left: 30px;
		p{
			font-weight: 500;
			padding-left: 10px;
			margin-bottom: 2px;
			@include fts(13,30);
			color: #969696;
			&.tx-blue{
				@include font-size(14.5);
			}
		}
	}
	&__file{
		width: 100%;
		margin-bottom: 5px;
		padding-left: 0;
		transition: 0.3s ease;
		&:hover{
			opacity: 0.7;
			transition: 0.3s ease;
		}
		label{
			padding: 32px 23px;
			display: block;
			height: auto;
		}
	}
	&__control{
		.btn_add{
			margin-left: 200px;
		}
	}
}

.mb-24{
	margin-bottom: 24px;
}