.js-reminder{
	.btn_style_01{
		display: none;
	}
	.btn_style_02{
		margin-left: 0;
	}
	textarea{
		pointer-events: none;
	}
	&.add{
		.btn_style_01{
			display: block;
		}
		.btn_style_02{
			display: none;
		}
		textarea{
			pointer-events: inherit;
		}
	}
}
.js-sales{
	.btn_style_01{
		display: none;
	}
	input{
		pointer-events: none;
		border: none;
		background: transparent;
	}
	&.add{
		.btn_style_01{
			display: block;
		}
		.btn_style_02{
			display: none;
		}
		input{
			pointer-events: inherit;
			border: 1px #ddd solid;
			background: #fff;
		}
	}
}

#page-T092{
	.statusicn{
		width: 63px;
	}
}