#page-T048{
	.btn_style_05{
		margin-bottom: 15px;
	}
}
.form-bock{
	width: 100%;
}
.calendar-flex{
	&__toolbar{
		@include flex(center,center);
		background: #F2F0EE;
		font-weight: bold;
		padding: 9px;
		color: #464646;
		margin-bottom: 25px;
		margin-top: 32px;
		@include font-size(20);
		&-center{
			margin: 0 49px;
		}
		button{
			border: 0;
			background: none;
			outline: none;
			box-shadow: none;
			width: 25px;
			height: 25px;
			cursor: pointer;
			display: block;
			.icon-left-arrow,.icon-right-arrow{
				position: relative;
				display: block;
				&::before{
					content: '';
					width: 5px;
					height: 5px;
					border-top: 2px solid #464646;
					border-right: 2px solid #464646;
					position: absolute;
					left: -4px;
					top: 50%;
					transform: rotate(45deg) translateY(-50%);
				}
			}
			.icon-left-arrow{
				&::before{
					transform: rotate(-135deg) translateY(-50%);
					top: -6px;
					left: 10px;
				}
			}
		}
	}
}
.calendar-content{
	position: relative;
	height: calc(100vh - 400px);
	// height: 100vh;
    // overflow-y: auto;
	>a{
		text-decoration: none;
		&:hover{
			opacity: 1;
		}
	}
	table{
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		table-layout: fixed;
		tr{
			th,td{
				border: 1px solid #E0E1E3;
			}
			th{
				width: calc(100%/7);
				padding: 12px;
				&:nth-child(1){
					background: #F6D5D0;
				}
				&:nth-child(7){
					background: rgba(#5BC0DE,0.41);
				}
				background: #F2F0EE;
				color: #464646;
				font-weight: bold;
				@include font-size(14.5);
			}
			td{
				//padding: 9px 21px;
				padding: .45em 1em;
				width: calc(100%/7);
				&:nth-child(1){
					background: rgba(#FED3CF,0.2);
				}
				&:nth-child(7){
					background: rgba(#5BC0DE,0.2);
				}
				font-weight: bold;
				color: #464646;
				// min-height: 200px;
				height: calc(100%/5) !important;
				//height: 199px;
				box-sizing: border-box;
				//@include font-size(20);
				font-size: clamp(12px, 1.66vw, 20px);
				@include mq(xl){
					@include font-vw(20,1200);
				}
				.label{
					display: block;
					margin-bottom: 2px;
					margin-top: 6px;
					background: #FED3CF;
					padding: 2px 7px;
					font-weight: 500;
					line-height: 1.5;
					text-decoration: none;
					@include font-size(14);
					font-size: min(12px, 1.16vw);
					//@include mq(xl){
					//	@include font-vw(14,1200);
					//}
					.status,
					.desc{
						display: block;
						overflow: hidden;
						text-overflow: ellipsis;
						white-space: nowrap;
					}
				}
			}
		}
	}
	.bg-gray{
		background: #F2F0EE;
	}
	.bg-pink{
		background: rgba(#FED3CF,0.2);
	}
}

.application-flex__btn--flex{
	@include flex(between);
	width: 100%;
}

.form-narrow__item--select{
	.form-narrow__select{
		width: 84px;
		margin-right: 8px;
	}
	span{
		font-weight: bold;
		margin-right: 10px;
		@include font-size(14.5);
	}
}

.form-narrow__item--people{
	.form_part_unit {
		width: 89px;
	}
}

.form-narrow__item--start{
	align-items: flex-start;
}

.form-narrow{
	&__list{
		
	}
	&__border{
		@include flex(0,center);
		border: 2px solid #F2F0EE;
		border-radius: 4px;
		padding: 8px 12px;
		margin-bottom: 20px;
		&:last-child{
			margin-bottom: 0;
		}
		&--start{
			align-items: flex-start;
			.form-narrow__check-left {
				padding-top: 9px;
			}
		}
	}
	&__check-left{
		width: 118px;
	}
	&__ct{
		width: calc(100% - 118px);
		.form-narrow__check{
			margin-top: 25px;
		}
		.form-narrow__select{
			width: 103px;
		}
		.flex-box{
			margin-top: 20px;
			@include flex(0,center);
			.form-narrow__check{
				margin-top: 0;
				margin-right: 10px;
			}
		}
	}
}

.mt-35{
	margin-top: 35px;
}

#page-T049{
	@include mq(xl-min){
		.looksort_table_info{
			min-width: 160px;
		}
		.tableblock_scroll table{
			width: 100%;
			min-width: 100%;
		}
	}
}
